<template>
	<div>
		<h2>Change Message</h2>
		<input type="text" v-model="input" v-on:keypress.enter="change" />
		<button v-on:click="change">change</button>
		<span>{{ output }}</span>
	</div>
</template>
<script>
export default {
	data() {
		return {
			input: '',
			output: 'default',
		}
	},
	methods: {
		change() {
			this.output = this.input;
			this.clearInput();
		},
		clearInput() {
			this.input = '';
		},
	},
}
</script>