<template>
	<div>
		<h2>Add List</h2>
		<input type="text" v-model="newListItem" v-on:keypress.enter="addItem" />
		<button v-on:click="addItem">add</button>
		<button class="clearBtn" v-if="newList.length > 0" v-on:click="clearAll">Clear All</button>
		<List v-bind:propsdata="newList" v-on:del-item="deleteItem"></List>
	</div>
</template>
<script>
import List from './List.vue';

export default {
	components: {
		List,
	},
	data() {
		return {
			newListItem: '',
			newList: [],
		}
	},
	methods: {
		addItem() {
			this.newList.push(this.newListItem);
			this.clearInput();
		},
		clearInput() {
			this.newListItem = '';
		},
		deleteItem(index) {
			this.newList.splice(index, 1);
		},
		clearAll() {
			this.newList = [];
		}
	}
}
</script>
<style scoped>
	.clearBtn {
		color: #fff;
		background: #41b883;
		margin-left: 0;
	}
</style>