<template>
	<div>
    <h2>Counter</h2>
    <button @click="reset" class="resetBtn">reset</button>
    <button @click="decrease">-</button>
    <button @click="increase">+</button>	
		<span>{{ count }}</span>
	</div>
</template>
<script>
export default {
	data() {
    return {
      count: 0,
    }
  },
  methods: {
    increase() {
      this.count++;
    },
    decrease() {
      this.count--;
    },
    reset() {
      this.count = 0;
    },
  },
}
</script>
<style scoped>
	button {
    width: 35px;
    height: 35px;
  }
  .resetBtn {
    width: auto;
  }
</style>